import React from "react"
import BurgerMenu from "../BurgerMenu/BurgerMenu"
import { useLocation, Link } from "react-router-dom"
import {useGlobalContext} from "../../context"

const Navbar = () => {
    const path = useLocation().pathname; 
    const { isDropDownNavOpen, setIsDropDownNavOpen } = useGlobalContext();

    return (
        <>
        <nav className="h-24 bg-white border-b border-black flex justify-center fixed z-50">
            <div className="h-full w-full md:w-4/5 flex justify-between items-center">
                <Link to="/" className="h-full w-3/5 sm:w-2/4 md:w-2/6">
                   <img src={`${process.env.REACT_APP_ASSETS_URL}/logo_v2.png`} alt="Tangerine Logo" className="object-contain h-full w-full"/>
                </Link>
                
                <div>
                    <BurgerMenu />
                </div>
            </div>
        </nav>
        <div className={isDropDownNavOpen ? "navActive" : "navNotActive"}>
            <ul className="w-full md:w-4/5 flex flex-col justify-around px-4">
                <li className={path === "/" ? `text-primary` : null} onClick={() => setIsDropDownNavOpen((isDropDownNavOpen) => !isDropDownNavOpen)}>
                    <Link to="/">
                        Home
                    </Link>
                </li>
                <li className={path === "/gallery" ? `text-primary` : null} onClick={() => setIsDropDownNavOpen((isDropDownNavOpen) => !isDropDownNavOpen)}>
                    <Link to="/gallery">
                        Gallery
                    </Link>
                </li>
                <li className={path === "/exhibitions" ? `text-primary` : null} onClick={() => setIsDropDownNavOpen((isDropDownNavOpen) => !isDropDownNavOpen)}>
                    <Link to="/exhibitions">
                        Exhibitions
                    </Link>
                </li>
                <li className={path === "/contact" ? `text-primary` : null} onClick={() => setIsDropDownNavOpen((isDropDownNavOpen) => !isDropDownNavOpen)}>
                    <Link to="/contact">
                        Contact
                    </Link>
                </li>
            </ul>
        </div> 
        
        </>
    )
}

export default Navbar