import React, { useState, useEffect } from "react"
import SliderCard from "./SliderCard/SliderCard"
import data from "./data"

const Slider = () => {
    const [index, setIndex] = useState(0)

    const checkIndex = (i) => {
    if (i > data.length - 1) {
      return 0;
    } else if (i < 0) {
      return data.length - 1;
    } else {
      return i;
    }
  };

    useEffect(() => {
    let slider = setInterval(() => {
      setIndex(checkIndex(index + 1));
    }, 4000);
    return () => clearInterval(slider);
    // eslint-disable-next-line
  }, [index]);

    return (
        <>
            {data.map((img, imageIndex) => {
                let slide = "nextSlide";
                if (imageIndex === index) {
                    slide = "activeSlide";
                } else if ( imageIndex === index - 1 || (index === 0 && imageIndex === data.length - 1)) 
                {
                    slide = "lastSlide";
                } else {
                    slide = "nextSlide";
                }
                return (
                    <div key={img.id} className={slide}>
                        <SliderCard img={img}/>
                    </div>
                    
                )
            })}
            <div className="flex justify-center mt-4">
                <svg xmlns="http://www.w3.org/2000/svg" className="h-10 w-10 mx-4 cursor-pointer" fill="none" viewBox="0 0 24 24" stroke="currentColor" onClick={() => setIndex(checkIndex(index - 1))}>
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
                <svg xmlns="http://www.w3.org/2000/svg" className="h-10 w-10 mx-4 cursor-pointer" fill="none" viewBox="0 0 24 24" stroke="currentColor" onClick={() => setIndex(checkIndex(index + 1))}>
  `                 <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                </svg>
            </div>
       </>
    );
}

export default Slider